import NavBar from "./NavBar";
import Home from "./Home";
import About from "./About";
import Contact from "./Contact";
import { useState, useEffect } from "react";
import { Route, Routes, Navigate, useLocation } from "react-router-dom";

import BackgroundHome from "../assets/home.jpg";
import BackgroundSAbout from "../assets/image4.jpg";
import BackgroundContact from "../assets/image2.jpg";

import "../Css/Body.css";

export default function Body() {
  const [X, setX] = useState(0);
  const [Y, setY] = useState(0);
  const [cursorClass, setcursorClass] = useState("");
  const [backimg, setBackimg] = useState(BackgroundHome);
  let currentPath = useLocation();

  useEffect(() => {
    document.addEventListener("mousemove", (e) => {
      setX(e.pageX - 12);
      setY(e.pageY - 12);
    });

    document.addEventListener("mousedown", (e) => {
      setcursorClass("Clicked");
    });
    document.addEventListener("mouseup", (e) => {
      setcursorClass("");
    });
    consoleIntro();
    switch (currentPath.pathname) {
      case "/about":
        setBackimg(BackgroundSAbout);
        break;
      case "/contact":
        setBackimg(BackgroundContact);
        break;
      default:
        setBackimg(BackgroundHome);
        break;
    }
  }, [currentPath.pathname]);
  const consoleIntro = () => {
    console.clear("");
    console.log("%c Hello ItzJvs ..!!", "font-weight: bold; font-size: 50px;color: white; text-shadow: 3px 3px 0 black,3px 3px 0 black , -2px 0px 0px white ,6px 6px 0px brown ");
    console.log(
      "%c SeekingJob (Entry Level..) : Fullstack , Java Developer , MERN Developer",
      "font-weight: bold; font-size: 20px;color: black; text-shadow: 3px 3px 20px cyan,3px 3px 0 brown"
    );
    console.log("%c Email : jagtapvs9832@gmail.com", "font-weight: bold; font-size: 20px;color: black; text-shadow: 3px 3px 20px cyan,3px 3px 0 brown");
    console.log("%c Contact :+917066649832", "font-weight: bold; font-size: 20px;color: black; text-shadow: 3px 3px 20px cyan,3px 3px 0 brown");
  };

  return (
    <div className="Body">
      <div id="Cursor" className={cursorClass} style={{ left: X + "px", top: Y + "px" }}></div>
      <div
        className="Body"
        style={{
          backgroundImage: `url(${backimg}) `,
          backgroundRepeat: "repeat",
          backgroundSize: "cover",
          backgroundAttachment: "fixed",
        }}
      >
        <NavBar />
        <Routes>
          <Route path="/" element={<Navigate to="/home" />} />
          <Route exact path="/home" element={<Home />} />
          <Route exact path="/about" element={<About />} />
          <Route exact path="/contact" element={<Contact />} />
        </Routes>
      </div>
    </div>
  );
}
