import { useEffect, useState } from "react";
import { NavLink } from "react-router-dom";

import "../Css/NavBar.css";
export default function NavBar() {
  const [link, setLink] = useState("https://drive.google.com/file/d/1-Occ8UJ2ZojIWC_6cAymsgGH54optr0x/view?usp=sharing");
  useEffect(() => {
    fetch("https://raw.githubusercontent.com/GitUserVivek/JsonServer/main/db.json").then(async (data) => {
      data = await data.json();
      data = data?.resume;
      if (data.link) {
        setLink(data.link);
      }
    });
  }, []);
  return (
    <div className="NavBarPage">
      <NavLink to="/home">Home</NavLink>
      <NavLink to="/about">About</NavLink>
      <a target="_blank" rel="noreferrer" href={link} download>
        Résumé
      </a>
      <NavLink to="/contact">Contact</NavLink>
    </div>
  );
}
